.q-panel{
  margin: 5% auto;
  border-radius: 16px;
  width: 100%;
  height: 100px;
  background-color: var(--nav-bg);
  box-shadow: 0px 4px 4px var(--internal-dark-shadow),
  4px 4px 40px var(--external-red-shadow);
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 100px;
  text-align: center;
  color: var(--black-font);
}